<script>
import api from '../../utils/api.js'
import { CONFIG, getPlatform } from '../../utils/config.js'
import platform from '../../utils/platform.js'

export default {
	data() {
		return {
			testing: false,
			testResults: [],
			lastResponse: null,
			platformInfo: {},
			currentPlatform: getPlatform(),
			apiBaseUrl: '',
			supportedFeatures: []
		}
	},
	async onLoad() {
		this.apiBaseUrl = CONFIG.API.DEV_BASE_URL;
		this.supportedFeatures = platform.features.getSupportedFeatures();
		
		// Ëé∑ÂèñÂπ≥Âè∞‰ø°ÊÅØ
		this.platformInfo = await platform.device.getDeviceInfo();
		
		this.addResult(true, `È°µÈù¢Âä†ËΩΩÂÆåÊàê - Âπ≥Âè∞: ${this.currentPlatform}`);
		this.addResult(true, `APIÂú∞ÂùÄ: ${this.apiBaseUrl}`);
		this.addResult(true, `ÊîØÊåÅÂäüËÉΩ: ${this.supportedFeatures.join(', ')}`);
		
		// Ê£ÄÊü•ÁΩëÁªúÁä∂ÊÄÅ
		const isConnected = await platform.network.isConnected();
		if (isConnected) {
			this.addResult(true, '‚úÖ ÁΩëÁªúËøûÊé•Ê≠£Â∏∏');
		} else {
			this.addResult(false, '‚ùå ÁΩëÁªúËøûÊé•ÂºÇÂ∏∏');
		}
	},
	methods: {
		addResult(success, message) {
			this.testResults.push({
				success,
				message,
				timestamp: new Date().toLocaleTimeString()
			});
		},
		
		async testConnection() {
			this.testing = true;
			this.addResult(true, 'ÂºÄÂßãÊµãËØïÂü∫Á°ÄËøûÊé•...');
			
			try {
				const recipes = await api.getAllRecipes();
				this.lastResponse = recipes;
				this.addResult(true, `‚úÖ ËøûÊé•ÊàêÂäüÔºÅËé∑ÂèñÂà∞ ${recipes.length} ‰∏™ËèúÂìÅ`);
				
				if (recipes.length > 0) {
					const firstRecipe = api.convertRecipeToDish(recipes[0]);
					this.addResult(true, `‚úÖ Êï∞ÊçÆËΩ¨Êç¢ÊàêÂäüÔºö${firstRecipe.name}`);
				}
			} catch (error) {
				this.addResult(false, `‚ùå ËøûÊé•Â§±Ë¥•Ôºö${error.message}`);
				console.error('Connection test failed:', error);
			} finally {
				this.testing = false;
			}
		},
		
		async testRecommendations() {
			this.testing = true;
			this.addResult(true, 'ÂºÄÂßãÊµãËØïÊé®ËçêÂäüËÉΩ...');
			
			try {
				const testPreferences = {
					healthy: 7,
					simple: 5,
					difficulty: 2,
					quick: 6,
					vegetarian: 4,
					spicy: 3
				};
				
				const recommendations = await api.getRecommendedRecipes(testPreferences);
				this.lastResponse = recommendations;
				this.addResult(true, `‚úÖ Êé®ËçêÂäüËÉΩÊ≠£Â∏∏ÔºÅËé∑ÂèñÂà∞ ${recommendations.length} ‰∏™Êé®Ëçê`);
				
				if (recommendations.length > 0) {
					const converted = recommendations.map(r => api.convertRecipeToDish(r));
					this.addResult(true, `‚úÖ Êé®ËçêËèúÂìÅÔºö${converted.slice(0, 3).map(d => d.name).join(', ')}`);
				}
			} catch (error) {
				this.addResult(false, `‚ùå Êé®ËçêÊµãËØïÂ§±Ë¥•Ôºö${error.message}`);
				console.error('Recommendations test failed:', error);
			} finally {
				this.testing = false;
			}
		},
		
		async testSearch() {
			this.testing = true;
			this.addResult(true, 'ÂºÄÂßãÊµãËØïÊêúÁ¥¢ÂäüËÉΩ...');
			
			try {
				const searchResults = await api.searchRecipes('È∏°Ëõã');
				this.lastResponse = searchResults;
				this.addResult(true, `‚úÖ ÊêúÁ¥¢ÂäüËÉΩÊ≠£Â∏∏ÔºÅÊâæÂà∞ ${searchResults.length} ‰∏™ÁªìÊûú`);
				
				if (searchResults.length > 0) {
					const converted = searchResults.map(r => api.convertRecipeToDish(r));
					this.addResult(true, `‚úÖ ÊêúÁ¥¢ÁªìÊûúÔºö${converted.slice(0, 3).map(d => d.name).join(', ')}`);
				}
			} catch (error) {
				this.addResult(false, `‚ùå ÊêúÁ¥¢ÊµãËØïÂ§±Ë¥•Ôºö${error.message}`);
				console.error('Search test failed:', error);
			} finally {
				this.testing = false;
			}
		},
		
		async testIngredientSearch() {
			this.testing = true;
			this.addResult(true, 'ÂºÄÂßãÊµãËØïÈ£üÊùêÊêúÁ¥¢...');
			
			try {
				const ingredientResults = await api.searchRecipesByIngredients(['Áï™ËåÑ', 'È∏°Ëõã']);
				this.lastResponse = ingredientResults;
				
				// Â§ÑÁêÜÈ£üÊùêÊêúÁ¥¢ÁöÑÁâπÊÆäÂìçÂ∫îÊ†ºÂºè
				const results = ingredientResults.results || ingredientResults;
				this.addResult(true, `‚úÖ È£üÊùêÊêúÁ¥¢Ê≠£Â∏∏ÔºÅÊâæÂà∞ ${results.length} ‰∏™ÁªìÊûú`);
				
				if (results.length > 0) {
					const converted = results.map(r => api.convertRecipeToDish(r));
					this.addResult(true, `‚úÖ È£üÊùêÊêúÁ¥¢ÁªìÊûúÔºö${converted.slice(0, 3).map(d => d.name).join(', ')}`);
					
					// ÊòæÁ§∫ÂåπÈÖçÂ∫¶‰ø°ÊÅØ
					if (results[0].matchPercentage !== undefined) {
						this.addResult(true, `‚úÖ ÂåπÈÖçÂ∫¶ËÆ°ÁÆóÊ≠£Â∏∏Ôºö${results[0].matchPercentage}%`);
					}
				}
			} catch (error) {
				this.addResult(false, `‚ùå È£üÊùêÊêúÁ¥¢ÊµãËØïÂ§±Ë¥•Ôºö${error.message}`);
				console.error('Ingredient search test failed:', error);
			} finally {
				this.testing = false;
			}
		}
	}
}
